body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}

body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  background-color: #111522;
  max-width: 350px;
  min-height: 200px;
  height: min-content;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #1b2136;
  border-radius: 8px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid #234668;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

button.subject {
  padding: 8px;
  font-size: 1.08rem;
  background: #252d4a;
  color: rgba(238, 238, 238, 0.911);
  border: none;
  transition: all 0.2s ease-in;
}

button.subject:hover {
  background: #252d4aa2;
  background: rgba(18, 202, 95, 0.712);
  color: rgb(255, 255, 255);
  transform: scale(1.06);
}
button.subject:disabled:hover {
  background: #cc16169c;
  color: rgb(255, 255, 255);
  transform: scale(1);
}

a {
  text-decoration: none;
}
p.pride {
  margin-top: 80px;
  margin-left: 80px;
  display: flex;
  background: linear-gradient(60deg, #c2237b 0%, rgb(15, 142, 146) 100%);
  padding: 20px;
  border-radius: 10px;
}
.pride a {
  font-size: 1.1em;
  text-decoration: none;
  margin-left: 8px;
}

#home {
  justify-content: center;
  border: none;
  position: absolute;
  margin-top: -100px;
  margin-left: -250px;
  width: 50px;
  font-size: 1.8rem;
  color: #ffffff;
  background-color: #222e58cc;
  border-radius: 40%;
  cursor: pointer;
}
